import React, { FC, SyntheticEvent, useState } from 'react'
import { useForm } from 'react-hook-form'
import * as yup from 'yup'
import { yupResolver } from '@hookform/resolvers/yup'
import FadeIn from 'react-fade-in'

import { 
  FormWrapper,
  FormSuperWrapper,
  RulesList,
  RuleItem,
  RulesTitle,
  RulesSubtitle,
  LinkButton,
  FormTitle,
  InputFormGroup,
  InputLabel,
  InputWrapper,
  Input,
  InputSymbolCount,
  SubmitButton,
  CheckboxWrapper,
  Checkbox,
  CheckboxInput,
  CheckboxLabel,
  CheckboxLink,
  InputError,
  CandyWrapper,
  CrossButton
 } from './styled'

import Axios from 'axios'
import { RulesBlock } from './RulesBlock'

interface FormInputs {
  message: string
  name: string
  email: string
  city: string
  recipientEmail: string
  rules: boolean
}

const schema = yup.object().shape<any>({
  message: yup.string().max(60, 'Ти перебiльшив з довжиною повiдомлення').required('Це поле не може бути пустим'),
  name: yup.string().max(14, 'Максимальна довжина 14 символiв').required('Це поле не може бути пустим'),
  city: yup.string().max(14, 'Максимальна довжина 14 символiв').required('Це поле не може бути пустим'),
  recipientEmail: yup.string().trim().email('Невiрний формат пошти'),
  email: yup.string().trim().email('Невiрний формат пошти').required('Це поле не може бути пустим'),
  rules: yup.bool().oneOf([true], 'Погодитись з правилами обов\'язково').required('Це поле не може бути пустим')
})



export const Form:  FC<{ setIsForm: () => void }> = ({ setIsForm  }) => {
  const { register, handleSubmit, errors } = useForm<FormInputs>({ resolver: yupResolver(schema) })
  const [formSended, setFormSended] = useState(false)
  const [openedRules, toggleRules] = useState(false)

  const [inputValues, setInputValues] = useState({
    message: '', name: '', city: '', rules: false
  })

  const onSubmit = async (data: FormInputs) => {
    const res = await Axios.post('https://antonipetrenko.space/wishes/', {
      name: data.name,
      city: data.city,
      email: data.email,
      destinationTo: [data.recipientEmail],
      wish: data.message

    })
    await res.status === 201 ? setFormSended(true) : setFormSended(false)
  }

  return (
    <FadeIn transitionDuration={2000}>
      <FormSuperWrapper>
<CandyWrapper />
        <FormWrapper onSubmit={handleSubmit(data => onSubmit(data))}>
          <CrossButton onClick={setIsForm}/>
          {!formSended && !openedRules ? (<>       <FormTitle>Заповніть форму</FormTitle>
              <InputFormGroup>
                  <InputLabel>Ваше вітання:</InputLabel>
              <InputWrapper>
                  <Input
                    onChange={(e: SyntheticEvent<HTMLInputElement, Event>) =>
                      setInputValues({ ...inputValues, message: e.currentTarget.value })
                    }
                    ref={register}
                    name="message"
                    type="text"
                  />
                  <InputSymbolCount>{inputValues.message?.length}/60</InputSymbolCount>
                  <InputError>{errors.message?.message}</InputError>
              </InputWrapper>
              </InputFormGroup>
              <InputFormGroup>
                  <InputLabel>Ваше ім’я:</InputLabel>
              <InputWrapper>
                  <Input
                    onChange={(e: SyntheticEvent<HTMLInputElement, Event>) =>
                      setInputValues({ ...inputValues, name: e.currentTarget.value })
                    }
                    ref={register}
                    name="name"
                    type="text"
                  />
                  <InputSymbolCount>{inputValues.name?.length}/14</InputSymbolCount>
                  <InputError>{errors.name?.message}</InputError>
              </InputWrapper>
              </InputFormGroup>
              <InputFormGroup>
              <InputLabel>Ваше місце проживання:</InputLabel>
                  <InputWrapper>
                  <Input
                    onChange={(e: SyntheticEvent<HTMLInputElement, Event>) => {
                      setInputValues({ ...inputValues, city: e.currentTarget.value })
                    }}
                    ref={register}
                    name="city"
                    type="text"
                  />
                              <InputSymbolCount>{inputValues.city?.length}/14</InputSymbolCount>
                <InputError>
                  {errors.city?.message}
                </InputError>
              </InputWrapper>
              </InputFormGroup>
              <InputFormGroup>
                  <InputLabel>Ваш email:</InputLabel>
              <InputWrapper>
                  <Input ref={register} name="email" type="email" />
                  <InputError>{errors.email?.message}</InputError>
              </InputWrapper>
              </InputFormGroup>
              <InputFormGroup>
              <InputLabel>Email одержувача *</InputLabel>
                  <InputWrapper>
                  <Input ref={register} name="recipientEmail" type="email" />
                  <InputError>{errors.recipientEmail?.message}</InputError>
              </InputWrapper>
              </InputFormGroup>
              <RulesList>
                  <RulesTitle> Будь ласка, звернiть увагу!</RulesTitle>
                  <RulesSubtitle> Вiтання може не пройти модерацiю, якщо воно мiстить:</RulesSubtitle>

              <RuleItem>Понад 60 символiв</RuleItem>
              <RuleItem>Грубi граматичнi помилки</RuleItem>
              <RuleItem>Ненормативну лексику, погрози, образливi высловлювання, заклики до насильства</RuleItem>
              <RuleItem>Комерцiйнi та рекламнi прояви</RuleItem>
              <RuleItem>Написанно не українською або англiйскою мовами</RuleItem>
              <RuleItem>Зверніть увагу, модерацію зможе пройти обмежена кількість привітань</RuleItem>
              </RulesList>
             <CheckboxWrapper>
              <Checkbox checked={inputValues.rules}><CheckboxInput checked={inputValues.rules} onClick={(e: SyntheticEvent<HTMLInputElement, Event>) => setInputValues({ ...inputValues, rules: e.currentTarget.checked })} name='rules' ref={register} type="checkbox"/></Checkbox>
                <CheckboxLabel>Я погоджуюся з </CheckboxLabel>
                <CheckboxLink onClick={() => toggleRules(!openedRules)}>умовами модерації</CheckboxLink>
              <InputError>
                {errors.rules?.message}
              </InputError>
             </CheckboxWrapper>
              <SubmitButton>Надіслати привітання</SubmitButton>
            </>
          ) : openedRules ? (
              <RulesBlock onClose={() => toggleRules(false)}/> )
            :         (     <FadeIn transitionDuration={3000}>
                <FormTitle>Дякуємо!<br/><br/>Новорічне вітання відправлено!<br/><br/>Очікуйте лист!</FormTitle>
              <LinkButton
                onClick={() => {
                  setFormSended(false)
                  setIsForm()
                }}>
                Домашня сторiнка
              </LinkButton>
            </FadeIn>
            )}
        </FormWrapper>
      </FormSuperWrapper>
    </FadeIn>
  )
}
