import React,  { FC, useState  } from 'react'
import FadeIn from 'react-fade-in'

import LogoImg from '../../img/logo.png'
import SofiaImg from '../../img/sofia.png'

import { CameraIcon } from '../../img/CameraIcon'
import {  Form  } from './Form'

import {
    Wrapper,
    ContentWrapper,
    CameraPopup,
    GreatlyText,
    SnowWrapper,
    MobileBallWrapper,
    MobileBall,
    Header,
    HeaderLeft,
    SofiaLogo,
    BallWrapper,
    CameraBlock,
    CameraIconWrapper,
    CameraDot,
    CameraTitle,
    Visa,
    Separator,
    LanguageSwitcher,
    Option,
    Logo,
    LogoTitle,
    Title,
    Subtitle,
    Text,
    ActionButton,
    HowItWorks,
    HowItWorksTitle,
    Item,
    ItemCircle,
    CircleBorder,
    ItemTitle,
    ItemSubtitle,
    ItemList,
    Copyright,
    Ball
  } from './styled'


export const Home:  FC = () => {
  const [currentLangOption, setCurrentLangOption] = useState<string>('ua')
  const [formIsOpen, toggleForm] = useState(false)

  const snowCount = Array.from(Array(100).keys())

  return (
    <Wrapper>
      <SnowWrapper>  {snowCount.map((item, index) => <div key={index} className="snow" />)}</SnowWrapper>

      {!formIsOpen ?
        <>(
          <ContentWrapper>
            <Header>
              <HeaderLeft>
                <SofiaLogo src={SofiaImg}/>
                <Visa/>
                <CameraBlock>
                  <CameraIconWrapper><CameraIcon/></CameraIconWrapper>
                  <CameraDot/>
                  <CameraTitle>online</CameraTitle>
                  <CameraPopup>Подорожуйте новорічними містами, не виходячи з дому! З 19 грудня по 7 січня спостерігайте на цьому сайті в режимі онлайн за Софійською площею, а також іншими святковими площами світу!</CameraPopup>
                </CameraBlock>
              </HeaderLeft>

              <LanguageSwitcher>
                <Option onClick ={() => setCurrentLangOption('ua')} active={currentLangOption === 'ua'}>Укр</Option> <Separator>/</Separator> <Option onClick ={() => setCurrentLangOption('eng')} active={currentLangOption === 'eng'}>EN</Option>
              </LanguageSwitcher>

            </Header>
            <FadeIn transitionDuration={2000}>
              <Logo src={LogoImg}/>
              <LogoTitle>
                    Iнтерактивна лазерна проекція
              </LogoTitle>
            </FadeIn>
            <FadeIn delay={200} transitionDuration={2000}>
              <Title>
                        Вітання звідусіль
              </Title>
            </FadeIn>
            <FadeIn delay={300} transitionDuration={2000}>

              <Subtitle>
                        Ваше привітання на головній новорічній площі України </Subtitle>
            </FadeIn>
            <FadeIn delay={600} transitionDuration={3000}>
              <Text>
                З 19 грудня 2020 року по 7 січня 2021 на Софійській площі у Києві буде працювати інтерактивна лазерна
                проекція, автором якої можете стати ви!
              </Text>

              <Text>
                Залиште вітання на цьому сайті, підпишіть його. І воно з'явиться у Новорічні свята на Дзвіниці
                Софійського собору. Ви зможете спостерігати за цим моментом онлайн на сайті і отримати фото на згадку на
                свій Email
              </Text>
              <Text>
                Проєкт створено Національним заповідником «Софія Київська» у партнерстві з Visa, щоб незважаючи на
                обставини, труднощі і складнощі, ви могли передати теплі слова тим, кого ви любите, де б ви зараз не
                були, з будь-якої точки планети. Ви зможете привiтати зі святами одне одного чи всю країну, конкретну
                людину чи цілу родину, того, хто поруч і того, хто через карантин залишився дуже далеко
              </Text>
            </FadeIn>
            <FadeIn delay={1000} transitionDuration={3000}>
              <GreatlyText>З прийдешнiми святами!</GreatlyText>
            </FadeIn>
          </ContentWrapper>
          <ActionButton onClick={() => toggleForm(true)}>Надіслати привітання</ActionButton>
          <BallWrapper>
            <FadeIn transitionDuration={1000}>
              <Ball transitionDuration="5s" animNumber={1} top="510px" left="60px">
                Вітання рідному Ужгороду! Дуже сумую! Інна, Нью-Йорк
                </Ball>
            </FadeIn>
            <FadeIn transitionDuration={1000}>
              <Ball transitionDuration="8s" animNumber={2} top="380px" right="60px">
                З новим роком, Андрійко. Твоя мама! Наталія, Кременчук
                </Ball>
            </FadeIn>
            <FadeIn transitionDuration={1000}>
              <Ball transitionDuration="4s" animNumber={3} top="823px" right="215px">
                Родину Пилипюків вітаю зі святами! Петро, Миколаїв
              </Ball>
            </FadeIn>
            <FadeIn transitionDuration={1000}>
              <Ball transitionDuration="8s" animNumber={1} top="943px" left="229px">
                Бабусю, дякую за те, що ти є! Обіймаю! Софія, Киів
              </Ball>
            </FadeIn>
            <FadeIn transitionDuration={1000}>
              <Ball transitionDuration="7s" animNumber={2} top="1111px" right="76px">
                Україно, нехай твій 2021-й буде сповнений перемог! Антон, Львів
              </Ball>
            </FadeIn>
          </BallWrapper>
          <MobileBallWrapper>
            <MobileBall transitionDuration="5s" animNumber={1} top='510px' left="60px">   Вітання рідному Ужгороду! Дуже скучаю! Інна, Нью-Йорк</MobileBall>
            <MobileBall transitionDuration="8s" animNumber={2} top='380px' right="60px">   Бабусю, дякую за те, що ти є! Обіймаю! Софія, Киів </MobileBall>
          </MobileBallWrapper>
          <HowItWorks>
            <FadeIn delay={200} transitionDuration={2000}>
              <HowItWorksTitle>Як це працює:</HowItWorksTitle>
            </FadeIn>
            <ItemList>
              <FadeIn delay={600} transitionDuration={2000}>

                <Item>
                  <ItemCircle><CircleBorder steps={1}/>01</ItemCircle>
                  <ItemTitle>Заявка</ItemTitle>
                  <ItemSubtitle>Заповніть форму з вашим вітанням українською або англійською мовами</ItemSubtitle>
                </Item>
              </FadeIn>
              <FadeIn delay={1200} transitionDuration={2000}>
                <Item>
                  <ItemCircle><CircleBorder steps={2}/>02</ItemCircle>
                  <ItemTitle>Підтвердження</ItemTitle>
                  <ItemSubtitle>Після перевірки вітання* чекайте на лист з точним часом його трансляції.</ItemSubtitle>
                </Item>
              </FadeIn>
              <FadeIn delay={1800} transitionDuration={2000}>
                <Item>
                  <ItemCircle><CircleBorder steps={3}/>03</ItemCircle>
                  <ItemTitle>Готово</ItemTitle>
                  <ItemSubtitle>У визначений час в режимі онлайн на цьому ж сайті слідкуйте, як ваше привітання з’явиться на дзвіницi Софійського собору. Ми також надішлемо фото цього моменту на електронну пошту, яку ви вказали.</ItemSubtitle>
                </Item>
              </FadeIn>
            </ItemList>
            <FadeIn delay={2000} transitionDuration={2000}>
              <ActionButton onClick={() => toggleForm(true)}>Надіслати привітання</ActionButton>
            </FadeIn>

          </HowItWorks>
          <Copyright>©Copyright 1996-2020. All Rights Reserved.</Copyright>
        </>
            :            <ContentWrapper>
          <Header>
            <HeaderLeft>
              <SofiaLogo src={SofiaImg}/>
              <Visa/>
              <CameraBlock>
                <CameraIconWrapper><CameraIcon/></CameraIconWrapper>
                <CameraDot/>
                <CameraTitle>online</CameraTitle>
              </CameraBlock>
            </HeaderLeft>

            <LanguageSwitcher>
              <Option onClick ={() => setCurrentLangOption('ua')} active={currentLangOption === 'ua'}>Ua</Option> <Separator>/</Separator> <Option onClick ={() => setCurrentLangOption('eng')} active={currentLangOption === 'eng'}>Eng</Option>
            </LanguageSwitcher>

          </Header>
          <Form setIsForm={() => toggleForm(false)}/>
        </ContentWrapper>
      }

    </Wrapper>
  )
}
